&.fonts {
	// display: block;
	> div.row {
		// background-color: red;
		text-align: center;
		padding: 1%;
		display: block;
		> div.column {
			margin: 1%;
			vertical-align: middle;
			display: inline-block;
			// background-color: #ccc;
			width: 20%;
			max-width: 500px;
			min-width: 300px;
		}
		&.primary {
			> div {
				min-height: 350px;
				padding: 1%;
				border-radius: 12px;
				// box-shadow: inset 0px 0px 1px 0px #e0dfdf;
				// border: 1px solid #e2e2e2;
				// box-shadow: inset 0px 1px 2px 1px rgba(var(--color-highlight), 0.1);
				// border: 1px solid rgba(var(--color-highlight), 0.5);
				// background-color: rgba(var(--color-main), 0.2);
				// background-color: var(--color-text-hover);
				// background-color: rgba(var(--color-main), 0.3);
				// box-shadow: inset 0px 0px 1px 1px rgba(var(--color-main), 0.3);
				background-color: rgb(var(--color-surround));
				h2 {
					display: block;
					padding: 4px 1px;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					font-size: 110%;
					font-weight: normal;
					// color: #666;
					// text-shadow: #ffffff 1px 1px 1px;
					color: var(--color-text-enable);
					text-shadow: var(--color-text-hover) 0px 1px 1px;
					span {
						display: block;
						margin-top: 7px;
						padding-top: 7px;
						font-size: 70%;
						// border-top: 1px solid #c5c9b6;
						text-shadow: var(--color-text-hover) 0px 0px 1px;
						border-top: 1px solid var(--color-text-hover);
					}
				}
				ul {
					list-style: none;
					li {
						display: block;
						margin: 5px 0;
						// color: #666666;
						color: var(--color-text-disable);
						a {
							// display: block;
							display: table;
							margin: 0 auto;
							font-size: 1em;
							line-height: 1em;
							// color: #039;
							color: rgba(var(--color-highlight), 0.9);
							span,
							em {
								display: table-cell;
								// display: inline-block;
								white-space: nowrap;
								text-overflow: ellipsis;
								overflow: hidden;
								vertical-align: middle;
								padding: 2px 7px;
							}
							span {
								padding-right: 4px;
								&:before {
									// color: $ColorMain;
									color: rgba(var(--color-highlight), 0.7);
								}
							}
							em {
								// display: inline-block;
								font-size: 0.7em;
								font-style: normal;
								// line-height: 2em;
								// height: 10px;
								min-width: 30px;
								border-radius: 50px;
								color: #fff;
								text-shadow: #666 0px 0px 1px;
								// background-color: $ColorMain;
								background-color: rgba(var(--color-main), 0.9);
								// padding:2px 7px 0px 7px;
							}
							&:hover em {
								// background-color: #74aede;
								color: rgba(rgb(255, 255, 255), 0.6);
								// background-color: rgba(var(--color-main), 0.4);
							}
							&.not {
								cursor: default;
								// color: lighten(#666, 20%);
								color: var(--color-text-disable);
								span {
									&:before {
										// color: lighten(#666, 20%);
										color: var(--color-text-disable);
									}
								}
								em {
									// background-color: lighten(#666, 30%);
									color: rgb(228, 227, 227);
									background-color: var(--color-text-hover);
								}
								&:hover {
									em {
										color: rgb(228, 227, 227);
									}
								}
							}
						}
					}
					&.keyboard {
					}
					&.typeface {
						padding: 2%;
						li {
							margin: 3px 0;
							display: inline-block;
							a {
								font-size: 80%;
								padding: 5px 12px;
								border-radius: 20px;
								// color: #333;
								// text-shadow: 0px 0px 1px #fff;
								// background-color: lighten($ColorMain, 10%);
								// color: $ColorBackground;
								// text-shadow: 0px 0px 1px $Color;
								background-color: rgba(var(--color-main), 0.7);
								color: #fff;
								text-shadow: 0px 0px 1px rgb(196, 192, 192);
								&:hover {
									color: darken(#fff, 10%);
									// background-color: rgba(var(--color-main), 0.4);
								}
							}
						}
					}
				}
				p {
					display: block;
					padding: 2% 7%;
					border-top: 1px solid var(--color-text-disable);
					font-size: 90%;
					a {
						display: block;
						padding-top: 3%;
						margin-top: 2%;
						border-top: 1px solid var(--color-text-disable);
						// color: #039;
						color: var(--color-text-disable);

						&:hover {
							text-decoration: underline;
						}
					}
				}
			}
		}
		&.gallery {
			ul {
				// background-color: red;
				display: block;
				list-style: none;
				li {
					display: inline-block;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					max-width: 350px;
					width: 94%;
					padding: 2%;
					margin: 1%;
					// background-color: #ccc;
					// background-color: #f9f9f9;
					position: relative;
					border-radius: 7px;
					// box-shadow: inset 0px 0px 1px 1px #ccc;
					// background-color: lighten(#ccc, 17%);
					box-shadow: inset 0px 0px 1px 1px rgba(var(--color-surround), 0.5);
					background-color: rgb(var(--color-surround), 0.9);
					// line-height: 19px;
					a {
						display: inline-block;
						&.dt {
							// color: #039;
							// color: rgba(var(--color-highlight), 0.9);
							color: var(--color-text-enable);
							text-shadow: var(--color-text-hover) 0px 1px 1px;
							&:hover {
								// color: #03c;
								color: rgba(var(--color-link-hover), 0.9);
								text-decoration: underline;
							}
							&:visited {
								// color: #666666;
								color: rgba(var(--color-link-visited), 0.9);
							}
							&.y {
								&::after {
									content: attr(data-total);
									position: absolute;
									display: inline-block;
									padding: 2px 5px;
									border-radius: 100px;
									font-size: 65%;
									// line-height:14px;
									margin-left: 2px;
									// color: #fff;
									// text-shadow: #666 0px -1px 1px;
									// background-color: lighten($ColorMain, 10%);
									color: #fff;
									text-shadow: #666 0px -1px 1px;
									background-color: rgba(var(--color-main), 0.9);
								}
							}
						}
						&.dl {
							position: absolute;
							top: 5px;
							right: 5px;
							width: 18px;
							height: 18px;
							overflow: hidden;
							// color: lighten($Color, 30%);
							color: rgba(var(--color-text), 0.8);
							&:before {
								padding: 1px 0px;
								margin: 1px;
								font-size: 100%;
							}
							&:hover {
								// color: $ColorMain;
								color: rgba(var(--color-text), 1);
							}
							&[href="#"] {
								display: none;
							}
						}
					}
					p {
						font-size: 80%;
						padding: 0px 5px;
						// line-height: 15px;
						&.f2 {
							font-size: 70%;
							// color: #666;
							color: var(--color-text-disable);
						}
					}
				}
			}
		}
		&.secondary {
			ul {
				li {
					a {
						&.dt {
							font-size: 120%;
							font-weight: normal;
						}
					}
					p {
					}
				}
			}
		}
		&.external {
			ul {
				li {
					width: 96%;
					padding: 1%;
					margin: 2px;
					// background-color: lighten(#ccc, 19%);
					// background-color: rgba(var(--color-surround), 1);
					// a {}
					// p {}
				}
			}
		}
		&.info {
			padding: 0%;
			font-size: 100%;
			> div {
				> * {
					overflow: hidden;
					text-overflow: ellipsis;
				}

				h1 {
					// NOTE: font-family
					font-size: calc(3vw + 110%);
					// line-height: 130%;
					// text-shadow: #000 0px 1px 1px; color: #ccc;
					// padding: 5% 0;
					// color: lighten(#666, 25%);
					// text-shadow: #000 0px -1px 1px;
					color: rgba(var(--color-text), 0.7);
					text-shadow: rgba(var(--color-text), 0.3) 0px -1px 1px;
				}
				h2 {
					// NOTE: font-subfamily
					font-size: calc(1vw + 15px);
					// color: #ccc;
					// text-shadow: #000 0px 1px 0px;
					color: rgba(var(--color-text), 0.8);
					text-shadow: rgba(var(--color-text), 0.6) 0px 1px 0px;
				}
				h3 {
					// NOTE: unique-identifier
					font-size: 80%;
					// color: #666;
					color: rgba(var(--color-text), 1);
				}
				h4 {
					// NOTE: full-name
					font-size: calc(1vw + 90%);
					// color: #666;
					color: rgba(var(--color-text), 0.6);
				}
				h5 {
					// NOTE: version
					font-size: calc(1vw + 60%);
					// color: #666;
					color: rgba(var(--color-text), 0.9);
				}
				h6 {
				}
				p {
					// color: #666;
					color: rgba(var(--color-text), 1);
				}
				a {
					display: inline-block;
					white-space: nowrap;
					max-width: 100%;
					// color: $ColorMain;
					// color: red;
					&:hover {
						text-decoration: underline;
					}
				}

				&.font-family {
					h1 {
					}
				}
				&.font-subfamily {
					h2 {
					}
				}
				&.unique-identifier {
					h3 {
					}
				}
				&.full-name {
					h4 {
					}
				}
				&.postscript-name {
					p {
					}
				}
				&.name {
					p {
					}
				}
				&.version {
					p {
					}
				}
				&.note {
					p {
					}
				}
				&.company {
					p {
					}
				}
				&.owner {
					p {
					}
				}
				&.url {
					p {
					}
				}
				&:empty {
					display: none;
				}
			}
			> ul {
				list-style: none;
				padding: 20px 0;
			}
		}
		&.more {
			margin: 0 auto;
			max-width: 630px;
			// box-shadow: 0px 1px 2px 1px #ccc;
			// border-radius: 3px;
			> div.toggle {
				position: relative;
				> ul {
					margin-bottom: 1%;
					li {
						@include user-select();
						list-style: none;
						display: inline-block;
						margin: 1%;
						padding: 5px 5%;
						// background-color: red;
						// border-top: 2px dashed #ccc;
						// border-bottom: 2px dashed #ccc;
						border-top: 2px dashed var(--color-text-disable);
						border-bottom: 2px dashed var(--color-text-disable);
						&:hover {
							border-top-color: rgba(var(--color-main), 1);
							border-bottom-color: rgba(var(--color-main), 1);
						}
						&.active {
							// border-top-color: $ColorMain;
							// border-bottom-color: $ColorMain;
							border-top-color: rgba(var(--color-main), 0.7);
							border-bottom-color: rgba(var(--color-main), 0.7);
						}
						&[data-toggle] {
							cursor: default;
						}
						&.get {
							// border-top-color: lighten($ColorMain, 20%);
							// border-bottom-color: lighten($ColorMain, 20%);
							border-top-color: rgba(var(--color-main), 0.9);
							border-bottom-color: rgba(var(--color-main), 0.9);
							a {
								// color: lighten($ColorMain, 7%);
								color: rgba(var(--color-main), 1);
							}
							&:hover {
								// border-top-color: darken($ColorMain, 20%);
								// border-bottom-color: darken($ColorMain, 20%);
								border-top-color: rgba(var(--color-main), 0.9);
								border-bottom-color: rgba(var(--color-main), 0.9);
								a {
									// color: darken($ColorMain, 20%);
									color: rgba(var(--color-main), 0.6);
								}
							}
						}
					}
				}
				> div {
					position: absolute;
					z-index: 999;
					border-radius: 12px;
					padding: 5%;
					width: 90%;
					display: none;
					transition: 1s;
					// text-align: justify;
					// background-color: #fff;
					// box-shadow: 0px 1px 2px 1px #ccc;
					// background-color: rgba(var(--color-surround), 1);
					background-color: rgba(var(--color-background), 1);
					box-shadow: 0px 1px 3px 1px rgba(var(--color-text), 0.3);
					&.active {
						display: block;
					}
					div {
						display: none;
						&.active {
							display: block;
						}
						h3 {
							padding: 3%;
							color: lighten(#666, 15%);
						}
						p {
							padding: 1% 1%;
						}
					}
				}
			}
		}
		@media #{$media-mobile} {
			padding: 0%;
			> div.column {
				margin: 3% 0%;
				width: 100%;
				min-width: 300px;
			}
			&.primary {
				> div {
					padding: 0%;
					box-shadow: inset 0px 0px 0px 0px #e0dfdf;
					border: 0px solid #e2e2e2;
					> h2 {
						// display: block; margin-top:7px;
						padding-top: 7px;
						border-top: 1px dotted #c5c9b6;
					}
				}
			}
			&.gallery {
				ul {
					li {
						display: block;
						max-width: 100%;
						background-color: transparent;
						border-bottom: 1px dotted #ccc;
						box-shadow: inset 0px 0px 0px 0px #ccc;
						&:last-child {
							border-bottom: 0;
						}
					}
				}
			}
		}
	}
}
