	//     >div{
	//       >div{
	//         &.notice{
	//           position: relative;
	//           padding-left:230px;
	//           &::before{
	//             content: '!';
	//             font-size: 14em;
	//             position: absolute;
	//             left: 15px; top: 0;
	//             // background-color: darken($ColorBackground,3%);
	//             // border-radius: 3px;
	//             color: lighten($ColorSecond,10%);
	//             height: 100%;
	//             width: 190px;
	//             // display: table-cell;
	//             text-align: center;
	//           }
	//         }
	//       }
	//     }
	//   }
	// }
	@media #{$media-mobile} {
		@import "Portal.define.result.mobile";
		&.notfound,
		&.pleaseenter {
			> div {
				> div {
					&.notice {
						// h1{
						//   font-size: 3em;
						// }
						// h2{
						//   font-size: 2em;
						// }
						ul {
							li {
								&::before {
									display: none;
								}
							}
						}
					}
				}
			}
		}
	}
}
