&.grammar {
	ol.mynum {
		list-style: none;
		li {
			&:nth-child(1):before {
				content: "၁";
			}
			&:nth-child(2):before {
				content: "၂";
			}
			&:nth-child(3):before {
				content: "၃";
			}
			&:nth-child(4):before {
				content: "၄";
			}
			&:nth-child(5):before {
				content: "၅";
			}
			&:nth-child(6):before {
				content: "၆";
			}
			&:nth-child(7):before {
				content: "၇";
			}
			&:nth-child(8):before {
				content: "၈";
			}
			&:nth-child(9):before {
				content: "၉";
			}
			&:nth-child(10):before {
				content: "၁၀";
			}
			&:nth-child(11):before {
				content: "၁၁";
			}
			&:nth-child(12):before {
				content: "၁၂";
			}
			&:nth-child(13):before {
				content: "၁၃";
			}
			&:nth-child(14):before {
				content: "၁၄";
			}
			&:nth-child(15):before {
				content: "၁၅";
			}
			&:nth-child(16):before {
				content: "၁၆";
			}
			&:nth-child(17):before {
				content: "၁၇";
			}
		}
	}
	table {
		border-collapse: collapse;
		// border: 1px solid black;
		vertical-align: middle;
		padding: 5px;
		margin: 10px 2px;
		// min-width: 400px;
		width: 90%;
		thead {
			// background-color: lighten(grey, 45%);
			background-color: rgba(var(--color-text), 0.1);
			// padding: 5px;
			// border-bottom: 1px solid lighten(grey, 20%);
		}
		th {
			font-size: 90%;
			&.chain {
				// color: lighten($ColorMain, 10%);
				color: rgba(var(--color-highlight), 0.9);
			}
		}
		// tr{
		//   // border-left: 1px solid black;
		// }
		// th,td {
		//   // padding: 5px 7px;
		//   padding: 5px;
		//   border: 1px solid lighten(grey, 20%);
		//   &:first-child{
		//     // border-left:0;
		//   }
		// }
		th,
		td {
			padding: 5px;
			border: 1px dotted lighten(grey, 20%);
			line-height: 30px;
			&:first-child {
				border-left: 0;
			}
			&:last-child {
				border-right: 0;
			}
		}
		td.chain {
			// color: darken($ColorMain, 0%);
			// background-color: lighten(grey, 47%);
			// color: rgba(var(--color-text), 0.7);
			color: rgba(var(--color-highlight), 0.9);
			background-color: rgba(var(--color-text), 0.1);
		}
	}
	section {
		padding: 15px;
		p {
			color: $ColorSecond;
			font-weight: lighter;
			font-size: 95%;
		}
		ol {
			padding-left: 15px;
			li {
				margin: 2px;
				&::before {
					display: inline-block;
					width: 30px;
					text-align: center;
					margin-right: 5px;
					// font-size: 90%;
					// color: $ColorSecond;
					// background-color: lighten($ColorMain, 35%);
					background-color: rgba(var(--color-main), 0.35);
					color: rgba(var(--color-highlight), 0.6);
					border-radius: 100%;
				}
				a {
					// background-color: lighten($ColorMain,40%);
					border-radius: 3px;
					display: inline-block;
					padding: 2px 7px;
					// color: darken($ColorMain, 20%);
					// color: rgba(var(--color-highlight), 0.8);
					&:hover {
						// background-color: lighten($ColorMain,20%);
						// color: darken($ColorRed, 0%);
					}
				}
			}
		}
	}
	> div.row {
		&.primary {
			padding: 1%;
			h1 {
				font-size: calc(3vw + 110%);
				line-height: 430%;
				text-shadow: #000 0px -1px 1px;
				// color:lighten($ColorMain,10%);
				color: rgba(var(--color-highlight), 1);
			}
			p {
				padding: 2px 1%;
				font-size: 120%;
				// color: $ColorSecond;
				color: var(--color-text-enable);
				q {
					color: rgba(var(--color-text));
				}
			}
		}
		div {
			&.detail {
				ol.word {
					margin: 1px 2%;
					padding: 5px 3%;
					line-height: 160%;
					// border: 1px solid lighten(#ccc,2%);
					&:before {
						display: inline-block;
						content: attr(data-name);
						// background-color: lighten($ColorMain, 10%);
						background-color: rgba(var(--color-main), 0.5);
						color: #fff;
						padding: 3px 10px;
						line-height: 150%;
						border-radius: 3px;
					}
					li {
						display: inline-block;
						margin: 1px 3px;
						// color: darken($ColorMain, 25%);
						color: var(--color-text-disable);
						// color: darken(#ccc,26%);
						&::after {
							content: "၊";
							color: var(--color-text-active);
						}
						&:last-child {
							&::after {
								content: "။";
							}
						}
					}
				}
				ul.worddesc {
					margin: 1px 2%;
					padding: 5px 3%;
					line-height: 160%;
					list-style: circle;
					white-space: break-spaces;
					// white-space: pre-line;
					// white-space: pre;
					li {
						// display: inline-block;
						margin: 1px 3px;
						// white-space: break-spaces;
						// white-space: nowrap;
						// white-space: pre-line;
						// color: darken($ColorMain,25%);
						// color: darken(#ccc,26%);
						strong {
							// color: darken($ColorMain, 10%);
							color: rgb(var(--color-highlight));
							font-weight: lighter;
						}
					}
				}

				ul.exam {
					margin: 1px 3%;
					padding: 1px 3%;
					list-style: none;
					font-size: 95%;
					// border: 1px dotted lighten(#ccc,1%);
					// background-color: lighten(#ccc,17%);
					border-radius: 7px;
					> li {
						position: relative;
						padding: 3px;
						&::before {
							content: attr(data-name);
							display: inline-block;
							width: 6px;
							height: 6px;
							border-radius: 50%;
							// background-color: lighten(#ccc,20%);
							// border: 1px solid darken(#ccc, 6%);
							border: 1px solid rgba(var(--color-text), 0.5);
							// background-color: lighten($ColorMain,35%);
							// border: 1px solid lighten($ColorMain,15%);
							position: absolute;
							top: 11px;
							left: -11px;
						}
						p {
							// color: lighten($ColorMain,15%);
							// color: darken(#ccc, 40%);
							color: rgba(var(--color-text), 0.8);
							em {
								font-style: normal;
								// color: darken($ColorMain, 12%);
								// color: rgba(var(--color-text), 1);
								&::before,
								&::after {
									content: '"';
								}
								&::before {
								}
								&::after {
								}
							}
							q {
								color: rgba(var(--color-text), 1);
							}
						}
						> ol {
							&:empty {
								display: none;
							}
							padding: 3px 2%;
							margin: 1px 1%;
							list-style: circle;
							li {
								// color: darken(#ccc, 20%);
								color: rgba(var(--color-text), 0.8);
								font-size: 90%;
								em {
									font-style: normal;
									// color: darken($ColorMain, 12%);
									color: rgba(var(--color-text), 1);
									&::before,
									&::after {
										content: '"';
									}
									&::before {
									}
									&::after {
									}
								}
							}
						}
						/*
