					}
				}
				> ol {
				}
				> div {
					padding: 1%;
					margin: 1% 2%;
					> h2 {
						// color: $ColorMain;
						color: rgba(var(--color-highlight), 1);
						font-weight: normal;
						font-size: 130%;
						line-height: 170%;
						padding: 1px 3%;
						margin: 0px 10px;
						display: inline-block;
						position: relative;
						&::before {
							content: "";
							display: inline-block;
							width: 25px;
							height: 25px;
							border-radius: 50%;
							// background-color: lighten($ColorMain,40%);
							// border: 2px solid lighten($ColorMain, 20%);
							border: 2px solid rgba(var(--color-highlight), 0.6);
							position: absolute;
							top: 8px;
							left: -24px;
						}
					}
					> div {
						// border-left: 2px solid lighten($ColorMain, 25%);
						border-left: 2px solid rgba(var(--color-highlight), 0.6);
						> p {
							padding: 1% 2%;
							// color: darken($ColorMain, 20%);
							color: var(--color-text-enable);
							border-radius: 3px;
							font-size: 120%;
							line-height: 170%;
							&:empty {
								&::before {
									content: "...";
									color: #ccc;
								}
							}
							q {
								color: var(--color-text-active);
							}
						}
						> div {
							margin: 1px;
							h3 {
								font-size: 110%;
								line-height: 160%;
								font-weight: normal;
								display: inline-block;
								padding: 2px 12px;
								margin: 2px 7px;
								// color: $ColorMain;
								color: rgba(var(--color-highlight), 0.6);
								text-shadow: #fff 1px 0px 1px;
								position: relative;
								&::before {
									content: "";
									display: inline-block;
									width: 18px;
									height: 18px;
									border-radius: 50%;
									// background-color: #fff;
									// border: 2px solid lighten($ColorMain, 25%);
									background-color: rgb(var(--color-background));
									border: 2px solid rgba(var(--color-highlight), 0.6);
									position: absolute;
									top: 6px;
									left: -20px;
								}
							}
							> div {
								> p {
									padding: 5px 3%;
									// color: #ccc;
									color: #666;
									// color: red;
								}
								> div {
									> h4 {
										display: inline-block;
										font-weight: normal;
										font-size: 104%;
										line-height: 160%;
										padding: 1px 2%;
										margin: 0px 9px;
										color: rgba(var(--color-highlight), 0.9);
										border-radius: 3px;
										position: relative;
										&::before {
											content: "";
											display: inline-block;
											width: 12px;
											height: 12px;
											border-radius: 50%;
											// background-color: #fff;
											// border: 2px solid lighten($ColorMain, 25%);
											background-color: rgb(var(--color-background));
											border: 2px solid rgba(var(--color-highlight), 0.6);
											position: absolute;
											top: 5px;
											left: -19px;
										}
									}
									> div {
										// margin-bottom:2%;
										> p {
											padding: 1% 3%;
											// color: #666;
											font-size: 95%;
											// color: red;
										}
										// > div {}
									}
								}
							}
						}
					}
				}
			}
		}
		ul {
			list-style: none;
			padding: 2px 1%;
			&.note {
				// text-align: center;
				li {
					vertical-align: middle;
					display: inline-block;
					margin: 2px;
					// color: lighten(#666, 10%);
					color: var(--color-text-disable);
					strong {
						// color: $ColorMain;
						color: rgba(var(--color-highlight), 1);
						display: inline-block;
						&::after {
							content: "-";
							padding-right: 2px;
							// color: #666;
							color: rgba(var(--color-highlight), 0.5);
							font-weight: normal;
						}
					}
					em {
						font-style: normal;
						color: darken($ColorMain, 12%);
						&::before,
						&::after {
							content: '"';
						}
						&::before {
							content: "(";
						}
						&::after {
							content: ")";
						}
					}
				}
			}
			&.partsofspeech {
				text-align: center;
				li {
					vertical-align: middle;
					display: inline-block;
					min-height: 100px;
					margin: 1%;
					border-radius: 12px;
					overflow: hidden;
					// background-color: lighten($ColorMain, 26%);
					background-color: rgba(var(--color-main), 0.2);
					a {
						font-size: 130%;
						display: block;
						padding: 15px;
						// color: #fff;
						// background-color: lighten($ColorMain, 10%);
						background-color: rgba(var(--color-main), 0.2);
						&:hover {
							// background-color: lighten($ColorMain, 20%);
							background-color: rgba(var(--color-main), 0.3);
							// text-shadow: #000 0px 0px 1px;
							@include box-shadow(
								0px,
								0px,
								1px,
								0px,
								rgba(var(--color-text), 0.3)
							);
						}
					}
					p {
						// color: #666;

						padding: 15px;
					}
					// &:hover {
					// 	// background-color: lighten($ColorMain, 30%);
					// 	background-color: rgba(var(--color-main), 0.3);
					// 	a {
					// 		@include box-shadow(0px, 0px, 1px, 1px, lighten(#666, 50%));
					// 	}
					// }
				}
			}
			&.summary-not-in-used {
				li {
					vertical-align: middle;
					display: inline-table;
					overflow: hidden;
					margin: 1%;
					border-radius: 12px;
					// background-color: lighten(#ccc, 16%);
					background-color: rgba(var(--color-main), 0.6);
					a,
					strong,
					p {
						display: table-cell;
						padding: 5px 15px;
						vertical-align: middle;
						font-weight: normal;
					}
					a,
					strong {
						font-size: 120%;
						// color: #666;
						text-shadow: #ccc 0px 1px 1px;
						// background-color: lighten(#ccc, 12%);
						background-color: rgba(var(--color-main), 0.2);
						position: relative;
						&:after,
						&:before {
							position: absolute;
							display: block;
							pointer-events: none;
							content: " ";
							width: 0px;
							height: 0px;
							text-align: center;
							vertical-align: middle;
							border: solid transparent;
							right: -19px;
							top: 32%;
						}
						&:before {
							// border-left-color: lighten(#ccc, 1%);
							border-left-color: rgba(var(--color-main), 0.1);
							border-width: 10px;
							right: -20px;
						}
						&:after {
							// border-left-color: lighten(#ccc, 12);
							border-left-color: rgba(var(--color-main), 0.4);
							border-width: 10px;
						}
					}
					p {
						color: #666;
						text-align: left;
					}
				}
			}
		}
	}
